<template>
  <section-container header-text="Reset password">
    <form @submit.prevent>
      <div v-show="error" class="form-message form-error-message">{{ error }}</div>
      <div v-show="emailSent" class="form-message form-success-message">
        We have sent you an email with a link to reset your password.
        After resetting your password, you will be able to <router-link to="/login">LOG IN</router-link>
      </div>

      <div class="input-group">
        <label for="email">Email</label>
        <input v-model="$v.email.$model" id="email" type="text" class="input-base" placeholder="example@gmail.com">
      </div>

      <button :disabled="$v.$invalid" @click="submit" type="submit" class="btn-primary submit-btn btn-margin-top">
        Continue
      </button>
    </form>
  </section-container>
</template>

<script>
import SectionContainer from '@/components/SectionContainer'
import { email, maxLength, required } from 'vuelidate/lib/validators'
import { mapActions } from 'vuex'

export default {
  name: 'ForgotPassword',
  components: { SectionContainer },
  data () {
    return {
      error: null,
      emailSent: false,
      email: ''
    }
  },
  validations: {
    email: {
      email,
      required,
      maxLength: maxLength(32)
    }
  },
  methods: {
    ...mapActions({
      resetPassword: 'resetPassword'
    }),
    submit () {
      this.error = null
      this.emailSent = false;

      if (!this.$v.invalid) {
        this.resetPassword(this.email)
          .then(() => {
            this.email = '';
            this.$v.$reset();
            this.emailSent = true;
          })
          .catch(error => {
            this.error = error.message
          })
      }
    }
  }
}
</script>

